import React , {useEffect} from "react";
import Aos from "aos";
import "aos/dist/aos.css";


function Services(){
    useEffect(() => {
        Aos.init({});
      }, []);
    return(
        <div class="services" id="services" >
            <h1 class="heading1"> </h1>
            <div class="service-collection">
                <div class="service" data-aos="zoom-in" data-aos-duration="800">
                        <div class="service-body">
                            <h3 class="service-heading">  </h3>
                            <img src={require("../images/teaching.png")} alt="" />
                        </div>
                </div>
                <div class="service" data-aos="zoom-in" data-aos-duration="800">
                    <div class="service-body">
                        <h3 class="service-heading"> </h3>
                        <img src={require("../images/designing.png")} alt="" />
                    </div>
                </div>
                <div class="service" data-aos="zoom-in" data-aos-duration="800">
                    <div class="service-body">
                        <h3 class="service-heading"> </h3>
                        <img src={require("../images/coding.png")} alt="" />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Services;