import React from "react";
import { Link } from 'react-scroll';

function handleIconMenuClick(){
    document.querySelector(".menu").classList.add("active");
    document.querySelector("body").classList.add("onscroll");
}

function Headerr() {
    return (
        <header>
            <div class="header" id="header">
                <nav>
                    <div class="brand">
                        Maryam Web
                    </div>
                    <div class="items">
                        <ul>
                            <li>
                                <Link to="about" spy={true} smooth={true} offset={50} duration={500}>
                                      
                                    <span class="iconify" data-icon="akar-icons:chevron-left" data-width="24" data-height="24"></span>                           
                                </Link>
                            </li>
                            <li>
                                <Link to="services" spy={true} smooth={true} offset={50} duration={500}>
                                     
                                    <span class="iconify" data-icon="akar-icons:chevron-left" data-width="24" data-height="24"></span>                           
                                </Link>
                            </li>
                            <li>
                                <Link to="works" spy={true} smooth={true} offset={50} duration={500}>
                                      
                                    <span class="iconify" data-icon="akar-icons:chevron-left" data-width="24" data-height="24"></span>                           

                                </Link>
                            </li>
                            <li>
                                <Link to="contact" spy={true} smooth={true} offset={50} duration={500}>
                                      
                                    <span class="iconify" data-icon="akar-icons:chevron-left" data-width="24" data-height="24"></span>                           

                                </Link>
                            </li>
                        </ul>
                    </div>
                    <button class="menu-icon" onClick={handleIconMenuClick}>
                        <span></span>
                    </button>
                </nav>
                <div class="image"> 
                    <img src={require("../images/laptop1.png")} alt="" />
                </div>
                <div class="wellcome">
                    <div class="circle"></div>
                    <p class="small-text"> <span role="img" aria-label=":)"></span></p>
                    <p class="large-text"> <span></span> .</p>
                    <p class="small-text">     <span role="img" aria-label=":)"></span></p>
                    <img src={require("../images/Watercolor-rb.png")} alt=""></img>
                </div>
            </div>
        </header>
  );
}

export default Headerr;