import React , {useState , useEffect} from "react";
import Aos from "aos";
import "aos/dist/aos.css";

function Contact(){
    useEffect(() => {
        Aos.init({});
      }, []);

    const [message , setMessage] = useState({
        fullName : "",
        phoneNumber : "",
        email : "",
        request : ""
    });

    function handleChange(event){
        const {value , name} = event.target ;
        setMessage(prevValue => {
            return{
                ...prevValue ,
                [name] : value
            }
        });
    }

    const [msg , setMsg] = useState("");
    async function handleSubmit(event){
        fetch('/sendMail', {
            // Adding method type
            method: "POST",
                
            // Adding body or contents to send
            body: JSON.stringify(message),
            
            // Adding headers to the request
            headers : {"Content-Type" : "application/JSON"}
        }).then((response)=>{
            console.log(response);
            if (!response.ok) {
                setMsg("   .    .");
                throw new Error(message);
            }
            else {
                setMsg("     .");
            }
        });
        event.preventDefault();
    }
    
    return(
        <div class="contact-me" id="contact">
            <div class="wrapper3">
                <div class="left-section">
                    <div class="contact-form" data-aos="fade-right" data-aos-duration="1000">
                        <form action="" method="" onSubmit={handleSubmit}>
                            <label for="fullname">   :</label>
                            <input type="text" name="fullName" class="input" onChange={handleChange} placeholder="   " required />
                            
                            <label for="phonenumber"> :</label>
                            <input type="text" name="phoneNumber" class="input" onChange={handleChange} placeholder=" " required />
                            
                            <label for="emailaddress"> :</label>
                            <input type="email" name="email" class="input" onChange={handleChange} placeholder=" " required />
                            
                            <label for="request">:</label>
                            <textarea name="request" id="" cols="30" rows="10" onChange={handleChange} placeholder=" " required="true"></textarea>
                            <button type="submit" class="btn send-btn" ></button>
                            <span class="message">{msg}</span>
                        </form>
                    </div>
                </div>
                <div class="right-section">
                    <div class="contact-info">
                        <h1 class="heading2">  </h1>
                        <p>  <img class="gift" src={require("../images/hand.gif")} alt=""/></p>
                        <p>     </p>
                        <p>      </p>
                        <p>      </p>
                        <p>    :</p>
                        <p> 
                            <span class="iconify" data-icon="charm:phone" data-width="20" data-height="20" data-flip="horizontal"></span>
                               : 
                            09211274790
                        </p>
                        <p>
                            <span class="iconify" data-icon="clarity:email-solid" data-width="20" data-height="20" data-flip="horizontal"></span>
                               :
                            maryam.gh1500@gmail.com
                        </p>
                        <p>              !</p>
                        <div class="social-medias">
                            <a class="icon" href="https://youtube.com/channel/UCORKvTA4dWeqyIDS0yj3zAw" target="_blank" rel="noopener noreferrer">
                                <span class="iconify" data-icon="akar-icons:youtube-fill" data-width="60" data-height="60"></span>
                                <span class="icon-name">Youtube</span>
                            </a>
                            <a class="icon" href="https://discord.gg/Dqm3zxad" target="_blank" rel="noopener noreferrer">
                                <span class="iconify" data-icon="bi:discord" data-width="60" data-height="60"></span>
                                <span class="icon-name">Discord</span>
                            </a>
                            <a class="icon" href="https://www.linkedin.com/in/maryam-gh-398455224" target="_blank" rel="noopener noreferrer">
                                <span class="iconify" data-icon="akar-icons:linkedin-v1-fill" data-width="60" data-height="60"></span>
                                <span class="icon-name">Linkedin</span>
                            </a>
                            <a class="icon" href="https://wa.me/+989211274790" target="_blank" rel="noopener noreferrer">
                                <span class="iconify" data-icon="cib:whatsapp" data-width="60" data-height="60"></span>
                                <span class="icon-name">Whatsapp</span>
                            </a>
                        </div>
                    </div>


                </div>
            </div>
        </div>
    )
}

export default Contact;