import React from "react";
import { Link } from 'react-scroll';

function handleMenuItemClick(){
    document.querySelector(".menu").classList.remove("active");
    document.querySelector("body").classList.remove("onscroll");
}
function Menu(){
    return(
        <div class="menu">
            <div class="menu-body">
                <button class="close-btn" onClick={handleMenuItemClick}>
                </button>
                <ul>
                    <li>
                        <Link to="header" spy={true} delay={600} smooth={true} offset={50} duration={500} onClick={handleMenuItemClick}>
                            
                        </Link>
                    </li>
                    <li>
                        <Link to="about" spy={true} delay={600} smooth={true} offset={50} duration={500} onClick={handleMenuItemClick}>
                              
                        </Link>
                    </li>
                    <li>
                        <Link to="services" spy={true} delay={600} smooth={true} offset={50} duration={500} onClick={handleMenuItemClick}>
                             
                        </Link>
                    </li>
                    <li>
                        <Link to="works" spy={true} delay={600} smooth={true} offset={50} duration={500} onClick={handleMenuItemClick}>
                              
                        </Link>
                    </li>
                    <li>
                        <Link to="contact" spy={true} delay={600} smooth={true} offset={50} duration={500} onClick={handleMenuItemClick}>
                              
                        </Link>
                    </li>
                </ul>
            </div>
        </div>
    )
}
export default Menu;